import React from "react";
import YouTube from "react-youtube";
import { throttle } from "lodash";

export default class SliderMain3 extends React.Component {
  state = { className: "home hide", videoHeight: "100%" };
  // state = { className: 'home', videoWidth: '100%', videoHeight: '100%' }
  // HFIEwpbOWak
  // mJhFtv5UTk8

  componentDidMount() {
    window.addEventListener("resize", this.handleResize);
    console.log("aaa");
    this.handleResize();
  }

  handleResize = throttle(() => {
    const ratio = window.innerWidth / window.innerHeight;
    const hp = (ratio / (16 / 9)) * 100;
    const clammedHeight = Math.max(hp, 100);
    const wp = 200 - hp;
    const clammedWidth = Math.max(wp, 100);
    this.setState({
      videoWidth: `${clammedWidth}%`,
      videoHeight: `${clammedHeight}%`
    });
  }, 100);

  render() {
    const opts = {
      height: "100%",
      width: "100%",
      playerVars: {
        autoplay: 1,
        rel: 0,
        autohide: 2,
        controls: 0,
        modestbranding: 1,
        disablekb: 1,
        iv_load_policy: 3,
        mute: 1,
        fs: 0,
        showinfo: 0,
        enablejsapi: 0,
        html5: 1,
        hd: 1,
        wmode: "opaque",
        playsinline: 1
      }
    };

    return (
      <div className={this.state.className} style={{ overflow: "hidden" }}>
        <div
          className="youtube-wrapper"
          style={{
            height: this.state.videoHeight,
            width: this.state.videoWidth
          }}
        >
          <YouTube
            videoId="HFIEwpbOWak"
            allowFullScreen
            opts={opts}
            onPlay={e => {
              this.setState({ className: "home" });
            }}
            onEnd={e => {
              e.target.playVideo();
            }}
          />
        </div>

        <div className="d-flex flex-column align-items-center justify-content-center blocker">
          <div style={{ fontSize: "32px", lineHeight: "34px" }}>
            Find your special tour today
          </div>
          <div
            style={{ fontSize: "54px", lineHeight: "60px" }}
            className="my-4"
          >
            With PJ Travel Tour
          </div>
          <div className="view-tour">VIEW TOURS</div>
        </div>

        <div className="home-bottom">
          <div className="d-flex h-100 align-items-center justify-content-center container">
            <div style={{ width: "20%", padding: "0 10px" }}>
              <input
                type="text"
                // value=""
                placeholder="Tour name"
                style={{
                  width: "100%",
                  height: "40px",
                  border: "1px solid #ddd",
                  padding: "0 10px",
                  outline: "0",
                  margin: "0"
                }}
              />
            </div>
            <div
              style={{
                width: "20%",
                padding: "0 10px"
              }}
            >
              <select name="tourtax[tour_phys]">
                <option value="0">Tour Type</option>
                <option value="escorted-tour">Escorted Tour</option>
                <option value="rail-tour">Rail Tour</option>
                <option value="river-cruise">River Cruise</option>
                <option value="tour-cruise">Tour &amp; Cruise</option>
                <option value="wildlife">Wildlife</option>
              </select>
            </div>
            <div
              style={{
                width: "20%",
                padding: "0 10px"
              }}
            >
              <select name="tourtax[destination]">
                <option value="0">Destination</option>
                <option value="brazil">Brazil</option>
                <option value="canada">Canada</option>
                <option value="cuba">Cuba</option>
                <option value="italy">Italy</option>
                <option value="philippines">Philippines</option>
                <option value="usa">USA</option>
              </select>
            </div>
            <div style={{ width: "20%", padding: "0 10px" }}>
              <select name="tourtax[month]">
                <option value="0">Month</option>
                <option value="january">January</option>
                <option value="february">February</option>
                <option value="march">March</option>
                <option value="april">April</option>
                <option value="may">May</option>
                <option value="june">June</option>
                <option value="july">July</option>
                <option value="august">August</option>
                <option value="september">September</option>
                <option value="october">October</option>
                <option value="november">November</option>
                <option value="december">December</option>
              </select>
            </div>
            <div style={{ width: "20%", padding: "0 10px" }}>
              <button
                type="submit"
                style={{
                  textTransform: "uppercase",
                  backgroundColor: "#3B2C18",
                  width: "100%",
                  color: "#CE9B51",
                  fontWeight: "700",
                  border: "none",
                  padding: "9px 8px 5px",
                  outline: "none",
                  boxShadow: "0 1px 0 0 rgba(255,179,0,.6)"
                }}
              >
                Search Tours
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
